tion () {\n          that.$axios.post('admin/SystemSetup/oillist', {\n            token: that.token,\n            page: that.index\n          })\n            .then(res => {\n              that.list = res.data.data.list;\n              that.showpage(res.data.data);\n            })\n            .catch(err => console.log(err))\n        }\n      })\n    },\n    addoil: function () {\n      var that = this;\n     \n      layer.open({\n        type: 2,\n        area: ['80%', '80%'],\n        title: ['详情', 'height:60px;background-color:#98D1F9;color:white;text-align:center;font-size:20px;line-height:60px;'],\n        shadeClose: true,\n        shade: 0.8,\n        content: 'http://localhost:8082' + '/#/systemSet/inclist',\n\n      })\n    }\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].name == 'FB军团大队名称') {\n              this.seCurId = arr[i].id;\n            }\n            //              if (arr[i].son) {\n            //                this.threeAuthList = arr[i].son;\n            //                for (var j = 0; j < arr[i].son.length; j++) {\n            //                  if (arr[i].action != arr[i].son[j].action) {\n            //                    arr[i].action = arr[i].son[0].action;\n            //                  }\n            //                }\n            //              }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', { type: 'error' });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  }\n}\n</script>\n<style scoped>\n.head {\n  z-index: 80;\n}\n\ntable tr td {\n  padding: 0;\n  width: 20%;\n}\n\ntable th {\n  background-color: #fff;\n}\n\ntd {\n